.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modalContent {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  max-width: 400px;
  width: 80%;
}

.closeButton {
  background-color: transparent;
  border: none;
  font-size: 16px;
  cursor: pointer;
  float: right;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.itemList {
  list-style: none;
  padding: 0;
  margin: 0 0 20px 0;
}

.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.removeItemButton {
  background-color: #e74c3c;
  color: white;
  padding: 5px 10px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.total {
  font-weight: bold;
}
